body {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    text-decoration: none;
  }
  
  #root{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .chat_btn{
    width: 70px;
    height: 70px;
    font-size: 35px;
    background-color: rgb(37, 211, 102);
    border-radius: 8px;
    position: fixed;
    outline: none;
    border: none;
    box-shadow: 0 4px 5px 0 rgb(0 0 0 / 20%), 0 0 3px 0 rgb(0 0 0 / 10%);
    color: white;
    bottom: 20px;
    cursor: pointer;
    right: 10px;
  }
  
  .navbar{
    width:100%;
    height: auto;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  
  .nav-1{
    display: flex;
    width: 100%;
    flex-direction: row;
  }
  
  .head_name{
    font-size: 30px;
    padding: 20px 50px;
    display: flex;
    font-family: Arial, Helvetica, sans-serif;
    color: red;
    flex-direction: row;
    width: 70%;
  }
  
  .head_name img{
    width: 100px;
    border-radius: 4px;
    margin: auto 0px;
    height: 80px;
  }
  
  .head_name h1{
    text-shadow: rgb(255 255 255 / 60%) 1px 1px 1px, rgb(0 0 0 / 60%) -1px -1px 1px;
    margin-left: 20px;
  }
  
  .head_name span{
    color: black;
  }
  
  .navbar ul{
    margin-top: 10px;
    list-style: none;
  }
  
  .navbar ul li{
    margin: 0px 5px;
    font-size: 25px;
    font-family: sans-serif;
    color: black;
    display: inline;
  }
  
  .navbar ul li a{
    color: black;
    padding: 7px 40px;
    border-right: 1px solid gray;
    text-decoration: none;
  }
  
  .navbar ul li a:hover{
    color: red;
  } 
  
  .navbar ul li span{
    padding: 7px 40px;
    border: none;
  }
  
  .head_btn{
    width: 30%;
    padding: 30px 50px;
  }
  
  .head_btn_1{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  
  .cart_btn{
    width: 110px;
    height:45px;
    outline: none;
    border: none;
    cursor: pointer;
    font-size: 23px;
    background-color: rgb(255,204,0);
    box-shadow: 0 4px 5px 0 rgb(0 0 0 / 20%), 0 0 3px 0 rgb(0 0 0 / 10%);
    border-radius: 50px;
  }
  
  .cart_btn:hover{
    background-color: rgb(255, 209, 27);
  }
  
  .login_btn{
    width: 150px;
    height:45px;
    outline: none;
    border: none;
    cursor: pointer;
    font-size: 23px;
    box-shadow: 0 4px 5px 0 rgb(0 0 0 / 20%), 0 0 3px 0 rgb(0 0 0 / 10%);
    border-radius: 4px;
  }
  
  .login_btn:hover{
    background-color: rgb(233, 230, 230);
  }
  
  .login_btn span{
    margin-left: 15px;
  }
  
  .search{
    padding-right: 10px;
    background-color: black;
    border-radius: 50px;
    width: 100%;
    margin-top: 25px;
    height: 45px;
  }
  
  #search_inp{
    outline: none;
    border-radius: 50px;
    border: none;
    font-size: 18px;
    background-color: black;
    height: 35px;
    width: 80%;
    color: white;
    margin: 0px 10px;
  }
  
  .search i{
    color: white;
    font-size: 22px;
    line-height: 45px;
  }
  
  .quote{
    margin-top: 20px;
    width: 95%;
    height: 630px;
    background: url('https://cdn.pixabay.com/photo/2015/12/04/14/49/hand-1076597_960_720.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  .div_quote_1{
    text-align: center;
    width: 70%;
    margin: auto;
    bottom: auto;
    color:rgb(255, 230, 143);
    font-size: 70px;
    text-shadow:rgba(0, 0, 0, 0.4) 0px 4px 5px;
    font-family: 'noticia text', serif;
  }
  
  .container{
    display: flex;
    flex-direction: column;
    width: 95%;
    padding-top: 5px;
    margin: auto;
    background-color: #f0efef;
    height: auto;
  }
  
  .about_us_para{
    text-align: center;
    height: 70px;
    line-height: 70px;
    font-size: 30px;
    font-weight: 700;
    letter-spacing: 0.5px;
    font-family: 'Questrial', sans-serif;
    color: rgb(255, 255, 255);
    margin: 0%;
    background-color: rgb(0, 0, 0);
  }
  
  .container_div{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
  }
  
  .container_quote, .explore_quote{
    display: flex;
    width: 100%;
    height: auto;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .container_quote_para, .explore_quote_para{
    width: 70%;
    text-align: center;
    font-size: 40px;
    text-shadow: rgb(255 255 255 / 60%) 1px 1px 1px, rgb(0 0 0 / 60%) -1px -1px 1px;
    font-weight: 700;
    letter-spacing: 0.5px;
    font-family: 'Questrial', sans-serif;
    color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0) !important;
  }
  
  .container_img{
    width: auto;
    display: flex;
    justify-content: center;
    height: 420px;
  }
  
  .container_img img{
    width: 600px;
    object-fit: cover;
    margin-top: 4px;
    height: 98%;
  }
  
  .learn_more_btn{
    width: 180px;
    height: 50px;
    outline: none;
    border: 2px solid black;
    font-size: 20px;
    font-weight: 600;
    border-radius: 4px;
    letter-spacing: 0.5px;
    cursor: pointer;
    transition: background-color 0.5s ease-out;
  }
  
  .learn_more_btn:hover{
    background-color: black;
    color: white;
    border: 2px solid white;
  }
  
  .parallax{
    margin: auto;
    width: 95%;
  }
  
  .parallax_div{
    width: 100%;
    height: 500px;
    margin: auto;
    background: url("https://static.wixstatic.com/media/dcca985fe5e148b1aea6744afd6af686.jpg/v1/fill/w_1363,h_460,al_c,q_85,usm_0.66_1.00_0.01/dcca985fe5e148b1aea6744afd6af686.webp") no-repeat center;
    background-size: cover;
  }
  
  .explore{
    display: flex;
    flex-direction: column;
    width: 95%;
    margin: auto;
    background-color: #f0efef;
    height: auto;
  }
  
  .explore_1{
    display: flex;
    flex-direction: row;
  }
  
  .explore_img{
    width: auto;
    display: flex;
    justify-content: center;
    height: 470px;
  }
  
  .explore_1_img img{
    width: 910px;
    object-fit: cover;
    margin-top: 4px;
    height: 470px;
  }
  
  .explore_quote button{
    border-radius: 50px;
  }
  
  .our_course_benifit{
    width: 95%;
    height: 70px;
    z-index: -1;
    color: white;
    margin: 50px auto;
    line-height: 70px;
    text-align: center;
    font-family: 'Questrial', sans-serif;
    justify-content: center;
    font-size: 45px;
    clip-path: polygon(0 0, 0% 100%, 98% 100%, 95% 50%, 98% 0);
    background-color: black;
  }
  
  .our_course_benifit p{
    margin: 0;
  }
  
  
  .benefit_details{
    display: flex;
    margin: auto;
    flex-direction: row;
    width:88%;
    padding: 20px 10px;
    justify-content: space-between;
    height: auto;
    background-color: #f9f9f9;
  }
  
  .benefit_details_1{
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 30%;
  }
  
  .benefit_details_1 img{
    width: 170px;
    height: 170px;
  }
  
  .benefit_details_1 .title{
    font-size: 30px;
    padding-bottom: 5px;
    font-weight: 700px;
    border-bottom: 2px solid black;
  }
  
  .benefit_details_1 .desc{
    text-align: center;
    line-height: 30px;
    font-family: 'Questrial', sans-serif;
  }
  
  .testimonial{
    margin: auto;
    padding: 30px 0px;
    text-align: center;
    font-size: 35px;
    font-family: 'Questrial', sans-serif;
    color: #E21C21;
    font-weight: 600;
  }
  
  
  .bgmedia{
    width: 95%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 500px;
  }
  
  .video-background { 
    width: 100%; 
    height: 100%; 
    object-fit: cover;
    overflow: hidden;
    z-index: -1;
  }
  
  .bgmedia p{
    z-index: 200;
    font-size: 60px;
    position: absolute;
    margin: 0%;
    margin-top: 120px;
    color: white;
    font-family: 'Questrial', sans-serif;
    text-align: center;
  }
  
  .bgmedia button{
    z-index: 200;
    font-size: 25px;
    margin: 0%;
    padding: 13px 15px;
    outline: none;
    position: absolute;
    border: none;
    margin-top: 240px;
    border-radius: 4px;
    background-color: #E21C21;
    color: white;
    cursor: pointer;
    box-shadow: 0 4px 5px 0 rgb(0 0 0 / 20%), 0 0 3px 0 rgb(0 0 0 / 10%);
    font-family: 'Questrial', sans-serif;
    text-align: center;
    transition: background-color 0.5s ease-in-out;
  }
  
  .bgmedia button:hover{
    background-color: white;
    color: black;
  }
  
  .App {
    font-family: sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 95%;
    height: 600px;
  }
  
  .copyright{
    width: 100%;
    height: 50px;
    margin: 0%;
    line-height: 50px;
    font-size: 20px;
    font-family: 'Questrial', sans-serif;
    color: white;
    text-align: center;
    background-color: rgb(255, 89, 55);
  }
  
  @media(max-width: 1370px){
    .explore_1{
      border-bottom: 7px solid white;
      flex-wrap: wrap;
    }
    .explore_quote{
      margin-bottom: 50px;
    }
    .explore_1_img img,.explore_1_img{
      width: 100%;
      margin-top: 0px;
    }
    .parallax_div{
      margin: 7px auto;
    }
  }
  
  @media(max-width: 1300px){
    .head_btn{
      width: 40%;
      padding: 30px 20px;
    }
  
    .head_name {
      width: 60%;
      padding: 20px 20px;
    }
  }
  
  @media(max-width: 860px){
    .nav-1{
      flex-direction: column;
      align-items: center;
    }
    .head_name {
      width: 95%;
      padding: 5px 5px;
    }
    .head_btn{
      width: 95%;
      
      padding: 5px 5px;
    }
    .head_btn_1{
      flex-direction: column;
      height: 120px;
      align-items: flex-end;
    }
    #search_inp{
      width: 90%;
    }
    .navbar ul{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-evenly;
      margin-top: 20px;
      width: 95%;
    }
    .navbar ul li a, .navbar ul li span{
      padding: 12px 15px;
      border: none;
      background-color: #4267B2;
      color: white;
      font-size: 18px;
      border-radius: 4px;
    }
  }
  
  @media(max-width: 520px){
    .head_name h1{
      font-size: 28px;
      margin-left: 8px;
      line-height: 40px;
    }
    .navbar ul{
      padding-left: 10px;
      line-height: 50px;
    }
    .navbar ul li{
      width: 47%;
    }
    .search {
      padding-right: 5px;
    }
    #search_inp{
      width: 85%;
      margin-top: 5px;
    }
    .chat_btn{
      width: 55px;
      right: 15px;
      height: 55px;
    }
    .div_quote_1{
      font-size: 45px;
    }
    .explore_quote_para{
      font-size: 30px;
    }
    .our_course_benifit{
      margin: 20px auto;
      font-size: 30px;
      clip-path: polygon(0 0, 0% 100%, 98% 100%, 90% 50%, 98% 0);
    }
  }
  
  @media(max-width: 940px){
    .container_div{
      flex-wrap: wrap-reverse;
    }
    .container_img img, .container_img{
      width: 100%;
    }
    .container_quote_para{
      font-size: 30px;
    }
    .container_quote{
      margin-bottom: 50px;
    }
    .benefit_details{
      flex-direction: column;
    }
    .benefit_details_1{
      width: 100%;
      margin-bottom: 30px;
    }
  }